import axios from 'axios';
/*
  __________________________https://books-backend.p.goit.global/books/category-list 
 ,     __https://books-backend.p.goit.global/books/top-books 
  _________________________https://books-backend.p.goit.global/books/category?category=selectedCategory
   ___________________https://books-backend.p.goit.global/books/ bookId
*/

const instance = axios.create({
  baseURL: 'https://books-backend.p.goit.global/books/',
});

export class BookAPI {
  getCategories() {
    return instance.get('category-list');
  }

  getBooksByCategories(selectedCategory) {
    return instance.get('category', {
      params: {
        category: selectedCategory,
      },
    });
  }

  getBooksTop() {
    return instance.get('top-books');
  }

  getBooksById(bookId) {
    return instance.get(`${bookId}`);
  }
}

